$main-color: #0e1116;
$secondaryColor: #374a67;
$borderColor: #616283;
$buttonColor1: #9e7b9b;
$buttoncolor: #cb9cf2;
$textColor: #f7f7f9;

body {
  background-color: $main-color !important;
}
.container {
  margin-top: 50px;
}
.settings {
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr;
  align-items: center;
  text-align: center;
  border: 1px solid $borderColor !important;
  background-color: $secondaryColor;
  color: $textColor;
  border-radius: 15px;
  .increment {
    background-color: $buttoncolor;
  }
  .decrement {
    background-color: $buttonColor1;
  }
  .btn:active {
    background-color: $secondaryColor;
    color: $textColor;
    border: 2px solid $buttonColor1;
  }
}
